import React, {Component} from 'react'
import { Grid, Header, Container, Form, Button, Transition, Message, TextArea } from 'semantic-ui-react'
import Aux from '../../hoc/Aux'
import './contact.scss'
import axios from 'axios'
import firebase from '../../firebase'
import Loader from '../Loader/loader'

class Contact extends Component {

  constructor (props) {
    super (props)
    this.state= {
      loading: false,
      email: '',
      full_name: '',
      message: '', 
      emailValid: false,
      formValid: false,
      mobile: null,
      response: null,
    }

    this.handleChange = this.handleChange.bind(this)
  }

  componentDidMount () {
    const body = document.querySelector('.contact-container').clientWidth
    window.addEventListener("resize", this.updateValue)

    if (body <= 768 ) {
      this.setState({
        mobile: true
      })
    } else if (body > 768) {
      this.setState({
        mobile: false
      })
    }
  }

  startLoading = () => {
    this.setState({
      loading: true
    })
  }

  endLoading = (status) => () => {
    this.setState( status === "success" ? {
      loading: false,
      email: '',
      full_name: '',
      message: '',
      response: 'Your message was sent successfully'
    } 
    : 
    { 
      loading: false,
      response: 'There was an error'
    })

    setTimeout(() => (this.setState({ response: null })), 3000)
  }

  updateValue = () => {
    const body = document.querySelector('.contact-container').clientWidth
    const mobile = body <= 768 ? true : false
    this.setState({
      mobile: mobile
    })
  }

  handleChange = (e, { name, value }) => {
    console.log(name, value)
    if ( name === "email") {
      
      this.setState(
        { [name]: value }, 
          () => { this.validateField(name, value)
      })
    } else {
      this.setState({ [name]: value })
    }
  }


  validateField = (fieldName, value) => {
    let emailValid = this.state.emailValid;
  
    switch(fieldName) {
      case 'email':
        var re = /^\s*(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))\s*$/;
        emailValid = re.test(String(value).toLowerCase());
        // emailValid = value.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i);
        break;
      default:
        break;
    }
    this.setState({
      emailValid: emailValid
    }, this.validateForm);
  }
  
  validateForm = () => {

    this.setState({
      formValid: this.state.emailValid
    });
  }

  sendMessage = () => {
    const { email, full_name, message, formValid } = this.state

    const { unload } = this.props

    if (formValid === false) {
      document.getElementById("email").focus()
    }

    if (formValid === true) {
      this.startLoading()

      axios.post('/contact-form', {
          "email": email,
          "name": full_name,
          "message": message
        }).then(response => {
        console.log(response);
        this.endLoading("success");
        unload();
      }).catch(error => {
        console.log(error);
        this.endLoading("fail")
      })
    }
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.updateValue)
  }

  render () {

    const { email, full_name, message, mobile, loading, response } = this.state;
    console.log(this.state)

    return (
      <Grid stackable className={'contact-container'}>
        <Grid.Column width={16}>

          <div className={"contact-form"}>

            <Header as="h2" textAlign="center">
              Contact Us
              <Header.Subheader>For requests and enquiries</Header.Subheader>
            </Header>

            {
              response &&
              
              <Transition.Group animation={'fly down'} duration={700}>
                <Container textAlign="center">
                  <p>{response}</p>
                </Container>
              </Transition.Group>
            }
            
            <Form>
              <Form.Input width={16} required label={"Full name"} value={full_name} name="full_name" placeholder='First name, Last name' onChange={this.handleChange} />
              
              <Form.Input id={'email'} width={16} required label={"Email address"} value={email} name="email" placeholder='Enter your email address' onChange={this.handleChange} />
              <Message
                error
                visible={(this.state.email.length > 0) && !this.state.emailValid}
                size="small"
              >
                <p>Email address is invalid</p>
              </Message>
              <Form.Field width={16}>
                <label>Message</label>
                <TextArea placeholder='Enter your request or enquiry' value={message} name="message" rows={4} onChange={this.handleChange}></TextArea>
              </Form.Field>
              <Button type='submit' floated="right" className={'primary-sub'} onClick={this.sendMessage}>Send</Button>
            </Form>
          </div>

        </Grid.Column>

        {
          loading && 

          <Loader loading={loading} message={"Sending"} />
        }
      </Grid>
    )
  }
}

export default Contact